import React from 'react'
import {connect} from 'react-redux'
const todos = (props) => {
    console.log(props)
    return (
        <div>
            {props.mydata.map((item,idx)=>(
                <li key={idx}>{item}<button onClick={()=>{
                    props.dispatch({
                        type:'DEL_TODO',
                        index:idx
                    })
                }}>删除</button></li>
            ))}

        </div>
    )
}

const mapStateToProps=(state)=>{
    return{
        mydata:state.todo
    }
}

export default connect( mapStateToProps)(todos)
