import React, { Component } from 'react'
import {NavBar,Icon,TabBar} from 'antd-mobile'
import { connect } from 'react-redux'


    
     class Details extends Component {
        constructor(props) {
            super(props);
            this.state = {
                selectedTab: 'redTab',
                
            }; 
        }
        componentDidMount(){
            let id=this.props.location.id
            console.log(this.props.todo);
            this.props.todo.map((item)=>{
                if(item.id==id){
                    this.setState({
                        selectedTab:'blueTab',
                    })
                }
            })
            console.log(this.props.location)
            
        }
        render() {
            let img=this.props.location.src
            let id=this.props.location.id
            let title=this.props.location.title
            return (
                <div>
                <NavBar mode="light" 
                icon={<Icon type="left" />} 
                onLeftClick={() => this.props.history.push('/home')}
                rightContent={
                <button style={{border:0,background:'white'}} onClick={()=>{this.props.history.push('/home')}}>取消</button>
               }>宝贝详情
               </NavBar>
               <img style={{width:'100%'}} src={this.props.location.src}/>
               <p>{this.props.location.title}</p>
               {console.log(this.props.location)}
               <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#ff5000"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="shop"
                        icon={
                            <i className='iconfont icon-dianpu1'></i>
                                              
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu'></i>                          
                        }
                        
                      
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu1'></i>
			
                             
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu'></i>                             
                        }
                        title="客服"
                        key="service"
                      
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item onClick={
                        console.log(this.props)
                    }
                        icon={
                            <i className='iconfont icon-shoucang1'></i>
          
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        title="收藏"
                        key="collect"
                        selected={this.state.selectedTab==='blueTab' }
                        onPress={ () => {
                            this.props.dispatch({
                                type:'ADD_TODO',
                                col:{img:img,id:id,title:title}
                            })
                            this.setState({
                                selectedTab:'blueTab'
                            })
                        } }
                    
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={<button style={{width:'100px',height:'35px',background:'#ffaa00',border:0,borderRadius:'50px 0 0 50px',color:'white',fontWeight:'bolder'}}>加入购物车</button>
                       }                     
                    >
			</TabBar.Item>
            <TabBar.Item
                        icon={<button style={{width:'100px',height:'35px',background:'#ff5000',border:0,borderRadius:'0 50px 50px 0',color:'white',fontWeight:'bolder'}}>立即购买</button>     
                       }                        
                    >
			</TabBar.Item>
            
                </TabBar>
            </div>
        
           </div>
            )
        }
    }
    
    const mapStateToProps=(state)=>({
        todo:state.todo
      })
      export default connect( mapStateToProps)(Details)
