
import {NavBar,Icon} from 'antd-mobile'
import Car from './Carousel'
import { connect } from 'react-redux'
import {goodslist} from './goods'
import React,{useState} from 'react'


const Home = (props) => {
  //console.log(props)
  props.dispatch(goodslist());
     return (
      <div>
      <NavBar mode="light" 
      style={{ background:'#ff8500',color:'white',width:'100%' }}
    icon={<img src={require('./tao.png')}/>} 
    onLeftClick={() => console.log('onLeftClick')}
    ><div style={{width:'300px',height:'30px',background:'#ff5000',border:0,color:'white',fontSize:'14px',borderRadius:'30px',textAlign:'center'}} onClick={()=>{props.history.push('/home/search')}}><Icon key="0" type="search" style={{ marginRight: '3px' }} />寻找宝贝店铺</div>
   </NavBar> 
   <Car style={{width:'100%'}}/>
      

   <ul style={{listStyle:'none'}}>
   {props.goods.map((item)=>{
          // console.log(this.props.goods)
          return (
          <li style={{float:'left',width:'50%'}}  
               onClick={()=>{
                 props.history.push({
                  pathname:'/home/details',
                  src:item.img,
                  id:item.id,
                  title:item.title
          })}
               }
               >
            <img style={{width:'100%'}}  src={item.img}/>
            <p style={{color:'red'}}>￥{item.price}</p>
            <p >{item.title}</p>
          </li>
          )
        })}
   </ul>

<div>.</div>
<div>.</div>
<div>.</div>

</div>

    )
}

const mapStateToProps=(state)=>({
  goods:state.goodlist.goods
})
export default connect( mapStateToProps)(Home)






