import React from 'react'
import {connect} from 'react-redux'
const todos = (props) => {
    console.log(props)
    return (
        <div>
            {props.mydata.map((item,idx)=>(
                <li key={item} style={{listStyleType:'none'}}>
                    <img src={item.img} style={{width:'100px',heigth:'100px'}}/>
                    <span style={{marginTop:'30px'}}>{item.title}</span>
                    <button onClick={(e)=>{
                    props.dispatch({
                        type:'DEL_TODO',
                        index:idx
                    })
                }} style={{width:'90px',height:'50px',backgroundColor:'#ff8500',color:'white',border:'0px'}}>删除</button></li>
            ))}

        </div>
    )
}

const mapStateToProps=(state)=>{
    return{
        mydata:state.todo
    }
}

export default connect( mapStateToProps)(todos)
